.our_projects {
    background: #F9F9F9;
    padding-top: 40px; }

.projects_carousel {
    position: relative;
    margin-top: 40px;

    .owl-prev,
    .owl-next {
        &:focus {
            outline: 0; } }

    .projects_next,
    .projects_prev {
        margin-top: 50px;
        margin-bottom: 45px;
        opacity: .5;
        transition: all .4s;
        &:hover {
            opacity: 1;
            transition: all .4s; } }

    .projects_next {
        padding-right: 60px; }

    .projects_prev {
        padding-left: 60px;
        transform: rotate(-180deg); } }

.project_item {
    width: 463px;
    height: 270px;
    background-position: center center;
    background-repeat: no-repeat;
    background-size: cover;
    position: relative; }

.project_item_text {
    font-size: 18px;
    line-height: 21px;
    color: #FFFFFF;
    text-shadow: 0px 1px 1px rgba(0, 0, 0, 0.03);
    position: absolute;
    bottom: 25px;
    left: 25px;
    margin: 0; }

