.slider_section, .slider {
    position: relative; }

.slider_item {
    background-position: center center;
    background-repeat: no-repeat;
    background-size: cover;
    height: 500px; }

.slider {
    .owl-nav {
        position: absolute;
        top: 0;
        height: 100%;
        width: 100%; } }

.slider_next,
.slider_prev {
    position: absolute;
    top: 50%;
    opacity: .5;
    transition: all .4s;
    &:hover {
        opacity: 1;
        transition: all .4s; } }


.slider_next {
    right: 50px;
    transform: translateY(-50%); }

.slider_prev {
    left: 50px;
    transform: translateY(-50%) rotate(-180deg); }

.slider_container {
    position: absolute;
    bottom: 0;
    z-index: 10;
    left: 50%;
    transform: translateX(-50%);

    display: flex;
    justify-content: space-between;
    align-items: flex-end; }

.slider_header {
    font-size: 48px;
    line-height: 57px;
    color: #FFFFFF;
    text-shadow: 0px 1px 1px rgba(0, 0, 0, 0.35);
    margin-bottom: 30px;
    span {
        font-family: 'HelveticaNeueCyr';
        font-weight: lighter;
        font-size: 36px; } }

.slider_text p {
    font-family: 'HelveticaNeueCyr';
    font-weight: lighter;
    font-size: 18px;
    line-height: 21px;
    color: #FFFFFF;
    text-shadow: 0px 1px 1px rgba(0, 0, 0, 0.35);
    max-width: 550px;
    margin-bottom: 50px; }

.owl-dots {
    margin-bottom: 30px; }

.owl-dot {
    all: unset;
    background-color: rgba(255, 255, 255, 0.3);
    width: 10px;
    height: 10px;
    border-radius: 50%;
    margin-left: 10px;
    display: inline-block;
    transition: all .3s;
    &.active, &:hover {
        background-color: rgba(255, 255, 255, 0.7);
        transition: all .3s; }
    &:focus {
        outline: none; } }
