header {
    border-top: 3px solid #FF6600; }


.header_container {
    min-height: 140px;
    display: flex;
    align-items: center;
    justify-content: space-between; }

.header_wrap,
.contact_item {
    display: flex;
    align-items: center; }

.contact_item {
    justify-content: flex-end;
    &:nth-child(1) {
        margin-bottom: 10px; }
    a {
        margin-right: 10px;
        color: #2E2E2E; } }

.header_contacts {
    padding: 0;
    margin-bottom: 0;
    margin-right: 50px; }

.logo {
    margin-right: 45px;
    height: 72px;
    width: auto; }

.header_description {
    margin: 0;
    font-size: 14px;
    font-weight: normal;
    line-height: 17px;
    color: #4D4D4D; }

.header_phone {
    font-size: 16px;
    font-family: 'HelveticaNeueCyr';
    font-weight: bold;
    line-height: 19px;
    &:hover {
        text-decoration: none; } }

.header_email {
    font-size: 14px;
    line-height: 17px;
    text-decoration: underline; }

.feedback {
    background: #FFF;
    border: 1px solid #FF6600;
    box-sizing: border-box;
    color: #FF6600;
    font-size: 14px;
    line-height: 16px;
    padding: 12px 46px;
    transition: all .4s;
    &:hover {
        background: #FF6600;
        color: #FFF;
        transition: all .4s; }
    &:focus {
        outline: none; } }

.header_menu_container {
    border-top: 1px solid #EAEAEA;
    box-shadow: 0px 4px 20px rgba(0, 0, 0, 0.05); }


.header_menu {
    display: flex;
    justify-content: space-between;
    margin-bottom: 0;
    height: 60px;
    list-style: none;
    li {
        position: relative; }

    a {
        text-decoration: none;
        font-size: 14px;
        color: #2E2E2E;
        display: block;
        height: 60px;
        line-height: 60px;
        text-transform: uppercase;
        transition: all .3s;
        &:before {
            content: '';
            position: absolute;
            top: -1px;
            left: 0;
            height: 1px;
            background-color: #FF6600;
            width: 0;
            transition: all .3s; }
        &:hover {
            color: #FF6600;
            transition: all .3s;
            &:before {
                width: 100%;
                transition: all .3s; } } } }

.current_page_item {
    a {
        color: #FF6600; }
    &:before {
        content: '';
        position: absolute;
        background-color: #FF6600;
        height: 1px;
        width: 100%;
        top: -1px; } }
